<template>
  <div class="subaccount_wrapper">
    <div class="subaccount_loading" v-show="loading"></div>
    <div v-show="!loading" class="subaccount_content_wrapper">
      <el-table :data="subAccountList" style="width: 98%" height="100%">
        <el-table-column
          prop="prop"
          :label="$t('subAccount.serialNumber')"
          width="width"
        >
          <template slot-scope="scope">
            <p>{{ scope.$index + 1 }}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="username"
          :label="$t('subAccount.username')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="password"
          :label="$t('subAccount.password')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="prop"
          :label="$t('subAccount.action')"
          width="width"
        >
          <template slot-scope="scope">
            <el-row :gutter="6">
              <el-col :span="12">
                <el-button
                  size="small"
                  type="primary"
                  @click="handleModify(scope)"
                  >{{ $t("btn.modify") }}</el-button
                >
              </el-col>
              <el-col :span="12">
                <el-button
                  :loading="dltLoading[scope.$index].loading"
                  size="small"
                  type="primary"
                  plain
                  @click="handleDelete(scope)"
                  >{{ $t("btn.delete") }}</el-button
                >
              </el-col>
            </el-row>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { startLoading, endLoading } from "@/util/loading.js";
export default {
  created() {
    this.dltLoading = this.getLoadingArray();
  },
  mounted() {
    this.loading = true;
    startLoading(".subaccount_loading");
    this.getSubAccount({
      role_id: this.$store.state.user.data.role_id,
    }).then(() => {
      this.loading = false;
      setTimeout(() => {
        endLoading();
      }, 0);
    });
  },
  data() {
    return {
      dltLoading: [],
      loading: false,
    };
  },
  methods: {
    getLoadingArray() {
      let array = new Array(this.subAccountList.length);
      let dltLoadingArray = array.fill({
        loading: false,
      });
      return dltLoadingArray;
    },
    handleModify(scope) {
      this.$router.push({
        name: "Add subAccount",
        params: scope.row,
      });
    },
    handleDelete(scope) {
      this.dltLoading.splice(scope.$index, 1, {
        loading: true,
      });
      let obj = {
        product_data: {
          id: scope.row.id,
        },
      };
      this.deleteSubAccount(obj).then(
        () => {
          this.dltLoading.splice(scope.$index, 1, {
            loading: false,
          });
        },
        () => {
          this.dltLoading.splice(scope.$index, 1, {
            loading: false,
          });
          this.$message.error(this.$t("error.serverErr"));
        }
      );
    },
    ...mapActions({
      getSubAccount: "user/getSubAccount",
      deleteSubAccount: "user/deleteSubAccount",
    }),
  },
  computed: {
    ...mapState({
      subAccountList: (state) => state.user.subAccount,
    }),
  },
};
</script>

<style lang="scss" scoped>
.subaccount_loading {
  padding: 3em;
}

.subaccount_content_wrapper {
  height: calc(100vh - 200px);
}
</style>
